import request from '@/utils/request'
// 获取验证码
export function registerList(params) {
    return request({
        url: '/checkin/admin/registerList',
        method:'get',
        params
    })
} 
export function quit(data){
    return request({
        url: '/checkin/admin/quit',
        method: 'post',
        data
    })
} 
export function businessRecord(data){
    return request({
        url: '/checkin/admin/businessRecord',
        method: 'post',
        data
    })
} 
export function isSubbranch(data){
    return request({
        url: '/checkin/login/isSubbranch',
        method: 'post',
        data
    })
} 
export function sendSms(data){
    return request({
        url: '/checkin/login/sendSms',
        method: 'post',
        data
    })
} 
export function deleteRegister(data){
    return request({
        url: '/checkin/admin/deleteRegister',
        method: 'post',
        data
    })
} 
export function deleteBusiness(data){
    return request({
        url: '/checkin/admin/deleteBusiness',
        method: 'post',
        data
    })
} 
export function login(data){
    return request({
        url: '/checkin/login/login',
        method: 'post',
        data
    })
} 
export function getCode(phone) {
    return request({
        url: '/api/Login/sendCode',
        method: 'post',
        data:{ phone }
    })
}  
export function userRegister(data){
    return request({
        url: '/checkin/user/userRegister',
        method: 'post',
        data
    })
} 
export function discernID(data){
    return request({
        url: '/checkin/user/discernID',
        method: 'post',
        data
    })
} 
export function upload(data){
    return request({
        url: '/checkin/user/upload',
        method: 'post',
        data
    })
} 
// 登录
export function resetPassword(params){
    return request({
        url: '/api/login/resetPassword',
        method: 'post',
        params:params
    })
} 
export function userLogin(phone,phone_code){
    return request({
        url: '/api/Login/checkLogin',
        method: 'post',
        data:{ phone,phone_code}
    })
} 
export function apply(data){
    return request({
        url: '/api/apply/apply',
        method: 'post',
        data
    })
} 
